import { useCallback, useEffect, useState } from "react";

import { GetDataByCrieteries } from "utils/api";
import { RandomKey, DateFromMilliseconds } from "utils/content";
import { Notify } from "components/app-notification/notification";

import styled from "styled-components"

const SParsel = styled.div`
    padding: 1rem;
    background: ${props => props.color ? props.color : 'white'};
    border: 1px solid black;
    border-radius: 10px;

    & .info {
        display: flex;
        justify-content: space-between;

        & .general_info {
            display: flex;
            flex-direction: column;

            & .weigth b {
                text-decoration: underline;
            }
        }

        & .other_info  {
            & .phones > * {
                margin: .5rem;
                padding: .5rem;
                font-size: 4rem;
                border-radius: 10px;
                cursor: pointer;
                transition: var(--transitionApp);

                &:hover {
                    background: var(--blueColor);
                }
            }

            & .expire {
                margin: .5rem;
            }
        }
    }

    & .photos {
        display: flex;
        flex-wrap: wrap;

        & span {
            max-width: 20vw;
            display: block;
            margin: 5px;
            border-radius: 5px;
            background: #000000a3;

            & img {
                width: 100%;
                height: 100%;
            }
        }
    }
`;

export default function Parsel({
    color, contactNumber, expireDatetime, from, id, isHaveWhatsUp, nickname, price, title, to, userID, weight,
    expireOnTopDatetime, fromID, onTop, toID, topTypeID, creationDatetime
}, isMy = false) {
    const [photos, setPhotos] = useState();

    const getPhotos = useCallback(async () => {
        const res = await GetDataByCrieteries("images", { "id": id });
        if (res?.err === "n/d") return setPhotos(null);
        if (res.err && res.err !== "ok") return Notify('fail', "Не удалось загрузить прикрепленные фото");
        return setPhotos(res?.data || res);
    }, [id]);

    useEffect(() => {
        if (photos === undefined) return getPhotos()
    }, [getPhotos, photos])

    return (
        <SParsel color={color}>
            <div className="info">
                <div className="general_info">
                    <span>{from}-{to}</span>
                    <span>{title}</span>
                    <span>Вес: {weight} кг</span>
                    <span className="weigth">Цена: <b> {price} </b> тг</span>
                </div>

                <div className="other_info">
                    <div className="phones">
                        {
                            isHaveWhatsUp &&
                            <a target="_blank" rel="noreferrer" href={`https://api.whatsapp.com/send?phone=${contactNumber}&text="Добрый день, пишу из приложения Жибек насчет вашей посылки: ${title}"`}>
                                <i className="fa fa-whatsapp" aria-hidden="true"></i>
                            </a>
                        }

                        <span onClick={() => window.open("tel:"+contactNumber)}>
                            <i className="fa fa-phone" aria-hidden="true"></i>
                        </span>
                    </div>
                    <div className="expire">
                        <span>Надо доставить до: {DateFromMilliseconds(expireDatetime)}</span>
                    </div>
                </div>
            </div>

            {
                photos &&
                <div className="photos">
                    {photos?.map(({ src }) => <span key={RandomKey()}><img src={src} alt="" /></span>)}
                </div>
            }

            {
                isMy &&
                <div className="manage">

                </div>
            }

        </SParsel>
    )
}